/**
 *
 * Grid
 *
 */

import React from 'react'
import { log } from 'ruucm-util'
import { map, isNil, isEmpty } from 'lodash'
import styled from 'styled-components'
import { Row, Column } from 'ruucm-blocks/layouts'

import { LittleFerryLoader } from 'ruucm-blocks/components/LoadingSpinners'
import { Center } from 'ruucm-blocks/css-patterns/centering'

const Wrapper = styled.div`
  max-width: 1050px;
  margin: 0 auto;
`

const Title = styled.h1`
  font-family: NanumSquareWeb;
  font-weight: 800;
  font-size: 120px;
  color: #38393c;
`
const Cateogry = styled.ul`
  position: relative;
  height: 93px;
`
const CateogryItem = styled.li``

const LoadingWrapper = styled.div``
const ContentsWrapper = styled.div``

const Contents = styled.div``

const Grid = props => {
  let contents = props[props.postType]

  return (
    <Wrapper>
      <Title>모아보기</Title>
      {isNil(contents) ? (
        <LoadingWrapper>
          <LittleFerryLoader waveColor="#525EF6" />
        </LoadingWrapper>
      ) : isEmpty(contents) ? (
        <LoadingWrapper>
          <div>준비 중입니다.</div>
        </LoadingWrapper> // No Data!
      ) : (
        <ContentsWrapper>
          <Cateogry>
            <Center axis="x">
              <CateogryItem onClick={() => props.getPosts({ category: 4 })}>
                eat
              </CateogryItem>
              <CateogryItem onClick={() => props.getPosts({ category: 5 })}>
                life
              </CateogryItem>
              <CateogryItem onClick={() => props.getPosts({ category: 6 })}>
                growth
              </CateogryItem>
              <CateogryItem onClick={() => props.getPosts({ category: 7 })}>
                love
              </CateogryItem>
              <CateogryItem onClick={() => props.getPosts({ category: 8 })}>
                tools
              </CateogryItem>
            </Center>
          </Cateogry>
          <Row>
            {map(contents, (data, id) => {
              return (
                <Column col="6" key={id}>
                  {data.title.rendered}
                </Column>
              )
            })}
          </Row>
        </ContentsWrapper>
      )}
    </Wrapper>
  )
}

export default Grid



